.primary-navigation__list {
    display: flex;

    li {
        a {
            span {
                padding-left: 1rem;
                padding-right: 1rem;
                border-right: solid 0.15rem $color-gray1;
            }
        }

        &:last-of-type {
            span {
                border-right: none;
            }
        }

        &:first-of-type span {
                padding-left: 0;
            }

    }
}

.secondary-navigation__list,
.primary-navigation__list,
.breadcrumbs {
    ul {
        list-style: none;
    }
    li {
        display: block;
    }
    span {
        @include content-style();
    }
}

.secondary-navigation__list {
    li {
        margin-right: 1rem;
    }
}

.primary-navigation {
    grid-column: 1/15;
}
.primary-search {
    grid-column: 16/-1;
}

.primary-navigation-search__wrapper {
    background-color: $color-library-bg;
    width: 100%;
}

.primary-navigation-search {

    color: $color-purewhite;
    padding-top: 2rem;

    @include media('<small') {
        @include default-margin();
    }

    @include media('>=small') {
        @include standard-box();
        @include standard-grid();

        > .library {
            grid-column: span 24;

        }
    }
}

.primary-navigation {
    a {
        @include anchor-reset-properties;
        color: $color-purewhite;
        // text-decoration: underline;
        border-bottom: 0;

        &:active {
            @include anchor-reset-properties;
        }

        &:hover {
            @include media('hover') {
                @include anchor-reset-properties;
                // text-decoration: underline;
                color: $color-purewhite;
                border-bottom: 0;
            }
        }

        &:visited,
        &:focus,
        &:active {
            color: $color-purewhite;
            border-bottom: 0;
            // text-decoration: underline;
        }

        &:focus,
        &:active {
            @include outline-properties($color-outline--dark);
            outline-style: $border-style-solid;
        }
    }



    .active {
        a {
            @include media('hover') {
                color: $color-nearblack;
            }

            &:visited {
                color: $color-nearblack;
            }

            &:focus,
            &:active {
                @include outline-properties($color-outline--dark);
                outline-style: $border-style-solid;
                color: $color-nearblack;
            }
        }
        span {
            background-color: $color-gray1;
            border-top: 0.3rem solid $color-gray1;
            border-bottom: 0.3rem solid $color-gray1;       
        }
    }

    li.normal:has(+ .active) {
        
            span {
                border-right-color: transparent;
            }
        

    }
}

.site-title {
    @include h1style();
}

.breadcrumbs,
.secondary-navigation {

    ol,
    .site-title {
        @include media('<small') {
            @include default-margin();
        }

        @include media('>=small') {
            @include standard-box();
        }

        display: flex;
        flex-wrap: wrap;
        list-style: none;
        padding-bottom: $measure--half;
        padding-top: $measure--half;

        li {
            @include content-style();
            @include smalltype();

            font-weight: $font-weight-text;
            line-height: $measure--small1;
            max-width: unset;
        }

        
    }
}

.breadcrumbs {
    background-color: $color-gray1;
    color: $color-nearblack;
    border-top: 0.015rem solid $color-gray7;
}

///// search box
.primary-search {


.search-box__query__input {
    @include font-family('default');
    @include body-font-features();
    @include alphatype();

    display: flex;
    flex-grow: 1;
    font-weight: $font-weight-text;
    padding: 0;
    position: relative;

    &:focus-within {
        @include outline-properties($color-purewhite);
        outline-style: $border-style-solid;
    }

    input {
        @include font-family('default');
        @include body-font-features();
        @include alphatype();

        border-color: $color-nearblack;
        border-style: $border-style-solid;
        border-width: $border-width-default;
        border-radius: $measure--quarter 0 0 $measure--quarter;
        font-style: normal;
        height: $measure--medium4;
        min-width: 0; // Constrains width in Firefox
        padding-left: $measure--half;
        padding-right: $measure--medium5;
        width: 100%;

        @include media('<mobile') {
            padding-right: $measure--basic;
        }

        &:focus-within {
            color: $color-gray10;
            font-style: normal;
            font-weight: $font-weight-medium;
            outline: 0;
        }

        &::placeholder {
            font-style: italic;
        }

        &:placeholder-shown {
            color: $color-gray7;
        }
    }

    .search-box__clear {
        @include epsilontype();

        align-items: center;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        height: $measure--small5;
        justify-content: center;
        position: absolute;
        right: $measure--half;
        top: $measure--quarter;
        width: $measure--small5;

        &:focus {
            @include outline-properties();
            outline-style: $border-style-solid;
            outline-offset: -#{$border-width-thicker};
        }

        .search-box__clear__text {
            @include sr-only();
        }

        .icon {
            /*
            // temp overide with xmark-temp-styling below

            --fa-primary-color: #{$color-gray7};
            --fa-primary-opacity: 1;
            --fa-secondary-color: #{$color-gray1};
            --fa-secondary-opacity: 1;
            */
            // @include xmark-temp-styling;

            letter-spacing: 0;
            line-height: 1;
        }

        &:hover {
            @include media('hover') {
                background-color: transparent;

                .icon {
                    /*
                    // temp overide with xmark-temp-styling below

                    --fa-primary-color: #{$color-gray1};
                    --fa-secondary-color: #{$color-gray7};
                    */
                    // @include xmark-temp-styling-hover;
                }
            }
        }
    }
}
}
